import React, {Suspense} from 'react';
import './body.css'
import Widget_l from './col_left/widget_l';
import Widget_r from './col_right/widget_r';
import About from "./col_mid/about/about";
import {BrowserRouter, Route} from "react-router-dom";
import Nav_user from "./nav-user/nav-user";
import PostsContainer from "./col_mid/posts-container";
import DialogInnerContainer from "./col_mid/dialogs/dialog-inner/dialog-inner-container";
import UsersContainer from "./col_mid/users/users-container";
import {withSuspense} from "../../hoc/withSuspense";

const DialogsContainer = React.lazy(() => import( './col_mid/dialogs/dialogs-container'));


const Body = (props) => {
    return (
        <BrowserRouter>
            <div className='main'>

                <div className='content'>
                    <div className='profile-banner-large bg-img'></div>

                    <Nav_user profile={props.profile}/>

                    <div className='container'>
                        <div className='row'>


                            <Widget_l profile={props.profile} status={props.status} updStatus={props.updStatus}/>



                            <Route path="/about" render={ () => <About/> } />

                            <Route path="/profile" render={ () => <PostsContainer/> } />

                            <Route path="/dialogs/1" render={ () => <DialogInnerContainer/>} />

                            <Route exact path="/dialogs"
                                   render={withSuspense(DialogsContainer)} />

                            <Route path="/friends" render={ () => <UsersContainer/> }/>


                            <Widget_r/>



                        </div>

                    </div>

                </div>
            </div>
        </BrowserRouter>
    )
}

export default Body;