import React from "react";
import s from "./users.module.css";
import Loader from "../../loader/loader";
import Pagenation from "../../../common/pagenation/pagenation";

let Users = (props) => {

    return (
        <div className='col-lg-5 col-friends'>
            <div className={s.friends_menu}>
                <div className={s.friends_menu_inner}>
                    <div className={s.friends_count}> Friends ({props.totalUsersCount})</div>
                    <div className={s.friends_nav}>
                        <div>All</div>
                        <div>Recently</div>
                        <div>Relative</div>
                        <div>Collage</div>
                        <div>Request</div>
                    </div>
                </div>
            </div>

            <div className={s.friends_page}>
                {
                    props.users.map(u => <div key={u.id} className={s.friends_inner}>
                        <div className={s.photo_friends}>
                          <a href={'/profile/' + u.id}><img src={u.photos.small}/></a>
                        </div>
                        <div>
                            <div className={s.name_friends}>  <a href={'/profile/' + u.id}>{u.name}</a></div>
                            <div className={s.invite_friends}>
                                {u.followed
                                    ? <button className={s.invite_btn_unfollow} disabled={props.followingInProgress.some(id => id ===u.id)} onClick={() => {

                                        props.unfollow(u.id)

                                    }}>Friend Request Sent</button>
                                    : <button disabled={props.followingInProgress.some(id => id ===u.id)} onClick={() => {props.follow(true, u.id)

                                        props.follow(u.id)

                                    }}>Add Friend</button>}
                            </div>
                        </div>
                    </div>)
                }
                {props.isFetching ? <Loader/> : null}

                <Pagenation totalItemsCount={props.totalUsersCount} pageSize={props.pageSize} onPageChanged={props.onPageChanged}/>

            </div>
        </div>

    )
}

export default Users;
