import React from "react";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../../../../../common/forms_control/FormsControl";
import {maxLengthCreator, required} from "../../../../../../utils/validator";

const maxLength50 = maxLengthCreator(50);

const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className='profile_search_form'>
            <Field className="share-text-field" component={Textarea} validate={[required, maxLength50]} name="newMsgText" placeholder="Enter message"/>
            <button className='profile_btn'>Send</button>
        </form>
    )
}

const AddMessageFormRedux = reduxForm({form: "dialogAddMessageForm"}) (AddMessageForm);

export default AddMessageFormRedux;