import React from 'react';
import './posts.css'
import MyPosts from './posts/MyPosts'
import AddPostFormRedux from "./add_post_form";

const Posts = (props) => {


    let PostsItem =
        [...props.postsData].reverse().map(Posts => <MyPosts Name={Posts.Name} Time={Posts.Time} Text={Posts.Text} Like={Posts.Like}
                                              Comment={Posts.Comment} Share={Posts.Share}/>);

    let onAddPost = (values) => {
        props.addPost(values.newPostText);
    }
    return (

        <div className='col-lg-5 order-2'>
            <div className='card card-small'>
                <div className='share-box-inner'>
                    <div className='profile_ph'>123213</div>
                    <div className='share-content-box w-100'>

                        <AddPostFormRedux onSubmit={onAddPost}/>

                    </div>
                </div>
            </div>

            {PostsItem}

        </div>
    )
}

export default Posts;

