import React from 'react';
import s from './MyPosts.module.css';
import BurgerMenu from "../../../burger_menu/burger_menu";

const MyPosts = React.memo(props => {

    console.log("render")

    return (
        <div className='card card-post'>
            <div className='lents'>
                <div className='post-title d-flex align-items-center'>
                    <div className={s.profile_author}>
                        <div className={s.author_photo}>
                            <a href='#'><img
                                src='https://demo.hasthemes.com/adda-preview/adda/assets/images/profile/profile-small-5.jpg'/></a>
                        </div>
                        <div className={s.name_author}>
                            <a href='#'>{props.Name}</a>
                            <p className={s.time_publ}>{props.Time}</p>
                        </div>
                        <BurgerMenu/>
                    </div>
                    <div className={s.posted_content}>
                        <p>{props.Text}</p>

                        <div className={s.post_meta}>
                            <div className={s.like}>
                                <button className={s.post_meta_like}>
                                    <i className="fas fa-heartbeat"></i>
                                    <span>You and {props.Like} people like this</span>
                                    <strong>{props.Like}</strong>
                                </button>
                            </div>

                            <div className={s.com_share}>
                                <div className={s.comments}>
                                    <button className={s.post_meta_like}>
                                        <i className="far fa-comments"></i>
                                        <span>{props.Comment}</span>
                                    </button>
                                </div>
                                <div className={s.share}>
                                    <button className={s.post_meta_like}>
                                        <i className="fas fa-share-alt"></i>
                                        <span>{props.Share}</span>
                                    </button>
                                </div>

                            </div>

                        </div>
                    </div>

                </div>

            </div>
        </div>
    )
})

export default MyPosts;