import React from 'react';
import './dialog-inner.css'
import MessageItem from './message-item/message-item';
import Top_bar from "./top-bar/top-bar";
import AddMessageFormRedux from "./top-bar/addmessagefrom";

let DialogInner = (props) => {

    let msgItem =
        props.dialogPage.msgData.map(message => <MessageItem id={message.id} name={message.name} text={message.text}/>);

    let addNewMessage = (values) => {
        props.addMsg(values.newMsgText);
    }

    return (

        <div className='col-lg-5'>
            <div className='card card_dialog-action'>

                <Top_bar/>

                <div className='dialog_content'>

                    {msgItem}


                </div>
                <div className='dialog_input'>
                    <AddMessageFormRedux onSubmit={addNewMessage}/>
{/*                    <form className='profile_search_form'>
                                <textarea
                                    ref={newMsgElement} onChange={onMsgChange} value={props.dialogPage.newMsgText}
                                    name="share" className="share-text-field" aria-disabled="true"
                                    placeholder="Enter message" data-toggle="modal" data-target="#textbox"
                                    id="email"/>
                        <button onClick={addMsg} type='button' className='profile_btn'>Send</button>
                    </form>*/}
                </div>

            </div>
        </div>
    )
}

export default DialogInner;