import React from 'react';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validator";
import {Textarea} from "../../common/forms_control/FormsControl";

let maxLength10 = maxLengthCreator(10);

const AddNewPostForm = (props) => {
    return (
        <form className='profile_search_form' onSubmit={props.handleSubmit} >
            <Field className="share-text-field" name="newPostText" component={Textarea} placeholder="Say Something"
            validate={[required, maxLength10]}/>
            <button className='profile_btn'>Share</button>
        </form>
    )
}

const AddPostFormRedux = reduxForm({form: "dialogAddPostForm"}) (AddNewPostForm);

export default AddPostFormRedux;