import React from 'react'
import {NavLink} from "react-router-dom";
import s from "../../../dialogs/dialog-inner/dialog-inner.css";

const Top_bar = () => {
    return (
        <div className='top_bar'>
            <div className='col-lg-4 col-dg-1'>
                <NavLink to="/dialogs">
                    <button className='btn_dialog-back'>
                        <i className="fas fa-chevron-left"></i>
                        <p>Back</p>
                    </button>
                </NavLink>
            </div>

            <div className='col-lg-5 col-dg-2'>
                <div className='dialog_name'>Alex White</div>
            </div>

            <div className='col-lg-4 col-dg-3'>
                <div className='right_dialog-bar'>
                    <div className={s.post_settings_bar}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </div>
                <figure className="profile-thumb-small profile-thumb-small-diag"><img
                    src="data:image/jpeg;base64,"
                    alt="profile picture"/></figure>
            </div>
        </div>
    )
}

export default Top_bar;