import React, {useEffect, useState} from "react";
import './profile_status.css'

const ProfileStatusWithHooks = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect( () => {
       setStatus(props.status);
    }, [props.status] );

    const activateMode = () => {
        setEditMode(true);
    }
    const deActivateEditMode = () => {
        setEditMode(false);
        props.updStatus(status);
    }

   const onStatusChange = (e) => {
       setStatus(e.currentTarget.value)
    }

    return (
        <div onClick={activateMode} className='status_author'>
            {!editMode &&
            <div className='user_status_srv'>
                {props.status
                    ?
                    <span className='user_status_active'>{props.status}</span>
                    : <span className='user_status'>Enter status</span>}
            </div>
            }
            {editMode &&
            <div>
                <input onChange={onStatusChange} onBlur={deActivateEditMode} className='header_search_field' autoFocus={true} value={status}
                />
            </div>
            }
        </div>
    )
}


export default ProfileStatusWithHooks;