import React from 'react';
import './nav-user.css'
import {NavLink} from "react-router-dom";
import default_profile from './../../../assets/img/default-user.png'

const Nav_user = (props) => {
    if (!props.profile) {
        return <loader/>
    }

    return (
        <div className='profile-menu-area bg-white'>
            <div className='container'>
                <div className='row align-items-center'>
                    <div className='col-lg-4 col-md-4'>
                        <div className='profile-picture-box'>
                            <figure className="profile-picture">
                                <a href="profile.html">
                                    {props.profile.photos.large === null
                                        ? <img src={default_profile} alt="profile picture"/>
                                        : <img src={props.profile.photos.large} alt="profile picture"/>
                                    }

                                </a>
                            </figure>
                        </div>
                    </div>
                    <div className='col-lg-5 col-md-5'>
                        <NavLink to="/profile">Profile</NavLink>
                        <NavLink to="/dialogs">Dialogs</NavLink>
                        <NavLink to="/about">About</NavLink>
                        <NavLink to="/friends">Friends</NavLink>
                        <a href="#" alt=''>More</a>
                    </div>
                    <div className='col-lg-6'>
                        <button className='edit_profile'>Edit Profile</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Nav_user;