import React from 'react';
import '../dialog-inner.css';

let MessageItem = (props) => {


    return(

            <div className='dialog_content_item'>

                <div className='dialog_item'>
                    <div className='dialog_item-photo'>
                        <figure className="profile-thumb-small profile-thumb-small-diag"><img
                            src="data:image/jpeg;base64,"
                            alt="profile picture"/></figure>
                    </div>
                    <div className='dialog_item_msg'>
                        <div className='dialog_name_msg'>{props.name}</div>
                        {props.text}
                    </div>

                </div>


            </div>


    )
}

export default MessageItem;