import React, {useState} from "react";
import s from "../../body/col_mid/users/users.module.css";

let Pagenation = ({totalItemsCount, pageSize, currentPage, onPageChanged, partitionSize = 5}) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize)

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / partitionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * partitionSize + 1;
    let rightPortionPageNumber = portionNumber  * partitionSize;

    return (
        <>
            {portionNumber > 1 &&
                <button onClick={() => { setPortionNumber(portionNumber - 1)}}>PREV</button>}

            {pages
                .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
                .map((p) => {
                    return <span key={p} onClick={(e) => {
                        onPageChanged(p);
                    }}>{p}</span>
                    })}
            {portionCount > portionNumber &&
            <button onClick={() => { setPortionNumber(portionNumber + 1)}}>NEXT</button>}

        </>
    )
}

export default Pagenation;
