import React from 'react';
import s from './header.module.css'
import default_profile from './../../assets/img/default-user.png'
import {NavLink} from "react-router-dom";

const Header = (props) => {

  return <header className={s.header}>
    <div className='container'>
      <div className="row align-items-center">
        <div className='col-lg-1 col-md-1'>
          <div><a href="#" alt=''>Home</a></div>
          <div><a href="#" alt=''>Message</a></div>
          <div><a href="#" alt=''>Notification</a></div>

        </div>
        <div className='col-lg-2 col-md-2'>
          <div className={s.header_logo}>
            <img src="https://demo.hasthemes.com/adda-preview/adda/assets/images/logo/logo.png" />
          </div>
        </div>
        <div className='col-lg-3 col-md-3'>
          <div>
            <form className={s.search}>
              <input type="text" placeholder="Search" className={s.search_field} />
              <button className={s.search_btn}><i class="fas fa-search"></i></button>
            </form></div>
          <div>{props.isAuth ? <div> <img className='default_photo' src={default_profile} alt="profile picture"/> <button onClick={props.logout}>loguot</button> </div> : <NavLink to="/login">Login</NavLink>}</div>
        </div>
      </div>
    </div>
  </header>
}

export default Header;