import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../../common/forms_control/FormsControl";
import {required} from "../../../utils/validator";
import {connect} from "react-redux";
import {login} from "../../../data/auth-reducer";
import {Redirect} from "react-router-dom";
import s from "./../../common/forms_control/FormsControl.module.css"

const LoginForm = ({handleSubmit, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div>
                <Field placeholder={"email"} name={"email"} validate={[required]} component={Input}/>
            </div>
            <div>
                <Field placeholder={"Password"} name={"password"} validate={[required]} component={Input} type={"password"}/>
            </div>
            <div>
                <Field type={"checkbox"} name={"remember me"} component={'input'}/> remember me
            </div>
            {error && <div className={s.form_error}>{error}</div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe);
    }

    if (props.isAuth) {
        return <Redirect to={"/profile"}/>
    }

    return (
        <div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Login);